//引入封装好的axios
import axios from './axios';
//添加分类
function add_(data) {
    return axios.get('/1km088d1', data)
} export {
    add_ as add,
}

function search_(data) {
    return axios.post('/1km088d1', data)
} export {
    search_ as search,
}

function del_(id) {
    return axios.post('/1km088d1', { uid: id })
} export {
    del_ as del,
}