import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

//命名空间
const userDemo = {
    namespaced: true,//开启命名空间
    state: {
        sum: 0, //初始化数据
        school: "abc",
        subject: "xyz"
    },
    mutations: {
        //+
        increment: (state, value) => (state.sum += value),
        //-
        decrement(state, value) {
            state.sum = state.sum - value;
        },
    },
    actions: {
        demo1(context, value) {
            console.log("处理了一些逻辑:demo1");
            context.dispatch("demo2", value); //如果逻辑多，可以多层往下传(第一个服务员)
        },
        demo2(context, value) {
            console.log("处理了一些逻辑:demo2");
            context.commit("increment", value);
        },
        demo3(context, value) {
            console.log("准备:demo3");
            setTimeout(() => {
                console.log("处理了一些逻辑:demo3");
                context.commit("increment", value);
            }, 3000);
        },
    },
    getters: {
        bigSum(state) {
            return state.sum * 10;
        },
    }
}
//命名空间
const personDemo = {
    namespaced: true,//开启命名空间
    state: {
        sname: 0,
    }
}

const store = new Vuex.Store({
    modules: {
        userDemo,
        personDemo
    }
});

//导出store
export default store;