import axios from 'axios';
import VueCookies from "vue-cookies";

// 如果是生产环境 使用.env.production配置文件中的VUE_APP_SERVER_URL作为请求前缀
// 如果是开发环境 使用.env.development配置文件中的VUE_APP_BASE_API作为前缀代理拦截
const BASE_URL =
    process.env.NODE_ENV === "production"
        ? process.env.VUE_APP_SERVER_URL
        : process.env.VUE_APP_BASE_API;

//设置axios
const service = axios.create({
    timeout: 1000 * 60,
    baseURL: BASE_URL,
    // 跨域情况下带上cookie
    withCredentials: true,
    headers: {
        "Content-Type": "application/json; charset=utf-8",
    },
})

//封装post请求
let post = function (url, data_ = {}) {
    return new Promise((resolve, reject) => {
        service.post(url, data_).then((res) => {
            return resolve(res)
        }).catch((err) => {
            return reject(err)
        })
    })
}

//封装get请求
let get = function (url, params) {
    return new Promise((resolve, reject) => {
        service.get(url, { params }).then((res) => {
            return resolve(res)
        }).catch((err) => {
            return reject(err)
        })
    })
}

//请求拦截
service.interceptors.request.use(config => {
    // 请求头带上token
    config.headers.token = VueCookies.get("token");
    return config
}, err => {
    return Promise.reject(err)
})

//添加响应拦截
service.interceptors.response.use(res => {
    return res.data
}, err => {
    return err
})

export default {
    post,
    get
}