<template>
    <div>
        <div>{{divShow}}</div>
        <el-button type="primary" @click="clickSet">保存数据</el-button>
        <el-button type="primary" @click="clickGet">获取数据</el-button>
        <el-button type="primary" @click="clickRemove">删除指定数据</el-button>
        <el-button type="primary" @click="clickRemoveAll">删除所有</el-button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                divShow:null,
            }
        },
        methods: {
            clickSet() {
                const currentTime = Date.now();
                const user = {name: 'zcc', currentTime: currentTime};
                localStorage.setItem('userJson', JSON.stringify(user));
            },
            clickGet(){
                var data = JSON.parse(localStorage.getItem('userJson'));
                this.divShow = data;
            },
            clickRemove(){
                localStorage.removeItem('userJson');
            },
            clickRemoveAll(){
                localStorage.clear();
            },
        },
    }
</script>

