import Vue from 'vue'
//引入VueRouter
import VueRouter from "vue-router";

//应用插件
Vue.use(VueRouter)

//防止重复点击
const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

import NetworkDemo from '@/components/demo/NetworkDemo'
import VuexDemo from '@/components/demo/VuexDemo'
import StoreDataDemo from '@/components/demo/StoreDataDemo'
import ModuleDemo from '@/components/demo/ModuleDemo'
import TestDemo from '@/components/demo/TestDemo'

export default new VueRouter({
  base: "/",
  // mode: "history",
  mode: "hash",
  routes: [
    {
      path: "/",
      name: "IndexPage",
      component: () => import("@/components/IndexPage.vue"),
    },
    {
      path: "/IndexDemo",
      name: "IndexDemo",
      component: () => import("@/components/demo/IndexDemo.vue"),
      children: [
        {
          path: "NetworkDemo",
          name: "NetworkDemo",
          component: NetworkDemo,
          props($route) {
            return {
              id: $route.query.id,
              page: $route.query.page,
            }
          }
        },
        {
          path: "VuexDemo",
          name: "VuexDemo",
          component: VuexDemo,
        },
        {
          path: "StoreDataDemo",
          name: "StoreDataDemo",
          component: StoreDataDemo,
        },
        {
          path: "ModuleDemo",
          name: "ModuleDemo",
          component: ModuleDemo,
        },
        {
          path: "TestDemo",
          name: "TestDemo",
          component: TestDemo,
        },
      ],
    },
  ],
});