<template>
    <div style="background-color:beige;">
        <div>当前sum：{{ $store.state.userDemo.sum }}</div>
        <div>当前bigSum：{{ $store.getters['userDemo/bigSum'] }}</div>
        <el-button type="primary" @click="btnClickAdd(1)">++数据</el-button>

        <el-button type="primary" @click="btnClickReduce(1)">--数据</el-button>
        <el-button type="primary" @click="btnClickAction1(1)">动作1</el-button>
        <el-button type="primary" @click="btnClickAction2(1)">动作2</el-button>
        <el-button type="primary" @click="btnClickAction3(10)">动作3</el-button>
    </div>
</template>

<script>
export default {
    methods: {
        //++数据
        btnClickAdd(value) {
            //alert("btnClickAdd");
            this.$store.commit('userDemo/increment', value);
        },
        //--数据
        btnClickReduce(value) {
            this.$store.commit('userDemo/decrement', value);
        },
        //动作1
        btnClickAction1(value) {
            this.$store.dispatch('userDemo/demo1', value);
        },
        //动作2
        btnClickAction2(value) {
            this.$store.dispatch('userDemo/demo2', value);
        },
        //动作3
        btnClickAction3(value) {
            this.$store.dispatch('userDemo/demo3', value);
        },

    },
}
</script>

