
/**
 * 随机数字。
 * @param {min} 最小值
 * @param {max} 最大值
 * @return {string} 返回字符串。
 */
export function generateRandomNumber(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

/**
 * 生成随机小数。
 * @param {min} 最小值
 * @param {max} 最大值
 * @param {decimalPlaces} 小数长度
 * @return {string} 返回字符串。
 */
export function generateRandomDecimal(min, max, decimalPlaces) {
    const randomDecimal = (Math.random() * (max - min) + min).toFixed(decimalPlaces);
    return parseFloat(randomDecimal);
}

/**
 * 生成随机字符串。
 * @param {length} 随机字符串长度。
 * @return {string} 返回字符串。
 */
export function generateRandomString(length) {
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    const randomValues = new Uint32Array(length);
    crypto.getRandomValues(randomValues);
    return Array.from(randomValues, value => characters[value % charactersLength]).join('');
}
