<template>
    <div>
        <el-button type="primary" @click="btnClick1">同步GET</el-button>
        <el-button type="primary" @click="btnClick2">同步POST</el-button>
        <el-button type="primary" @click="btnClick3">异步POST</el-button>
        <el-button type="primary" @click="btnClick4">同步POST单参数</el-button>
        <div></div>
    </div>
</template>

<script>
import { add, search, del } from "@/api/userApi";

export default {
    mounted () {
        console.log('currentRoute',this.$router.currentRoute.name);
        console.log('currentRoute',this.$router.currentRoute);
    },
    methods: {
        async btnClick1() {
            let res = await add({
                a: 'a1'
            });
            console.log(res);
        },
        async btnClick2() {
            let res = await search({
                b: 'b2'
            });
            console.log(res);
        },
        btnClick3() {
            search({
                c: 'c3'
            }).then((res) => {
                console.log(res);
            }).catch((err) => {
                console.log(err);
            })
        },
        async btnClick4() {
            let res = await del(999);
            console.log(res);
        },
    },
}
</script>

