<template>
    <div>
        <div>{{divShow}}</div>
        <el-button type="primary" @click="clickSet">设置cookies</el-button>
        <el-button type="primary" @click="clickGet">获取cookies</el-button>
        <el-button type="primary" @click="clickRemove">删除cookies</el-button>
        <el-button type="primary" @click="clickIsKey">查看cookies</el-button>
        <el-button type="primary" @click="clickKeys">获取所有cookies</el-button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                divShow:null,
            }
        },
        methods: {
            clickSet() {
                const currentTime = Date.now();
                this.$cookies.set('token', currentTime, 60*60*24*7);
            },
            clickGet(){
                this.divShow = this.$cookies.get('token');
            },
            clickRemove(){
                this.$cookies.remove('token');
            },
            clickIsKey(){
                let state = this.$cookies.isKey('token');
                alert(state);
            },
            clickKeys(){
                let arr = this.$cookies.keys();
                var jsonSTR = JSON.stringify(arr);
                alert(jsonSTR);
            }
        },
    }
</script>

