<template>
    <div>
        <div>{{divShow}}</div>
        <el-button type="primary" @click="clickSet">保存数据</el-button>
        <el-button type="primary" @click="clickGet">获取数据</el-button>
        <el-button type="primary" @click="clickRemove">删除指定数据</el-button>
        <el-button type="primary" @click="clickRemoveAll">删除所有</el-button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                divShow:null,
            }
        },
        methods: {
            clickSet() {
                const currentTime = Date.now();
                sessionStorage.setItem('key', currentTime);
            },
            clickGet(){
                let data = sessionStorage.getItem('key');
                this.divShow = data;
            },
            clickRemove(){
                sessionStorage.removeItem('key');
            },
            clickRemoveAll(){
                sessionStorage.clear();
            },
        },
    }
</script>

